import * as React from "react";
import { Typography } from "@material-ui/core";

import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';

import { ButtonBase } from '@material-ui/core';

const useStyles = makeStyles({

    eventContainer: {

        width: "100%",
        height: "112px",

        paddingLeft: "10px",
        paddingRight: "10px",

        display: "flex",
        flexDirection: "row",
        justifyContent: "center",
        alignItems: "center",
        backgroundColor: "white",
        borderRadius: "15px",

        '&:hover': {
            backgroundColor: "#F5F5F5"
         }

    },

    notImage: {

        width: "64px",
        height: "64px",

    },

    info: {


        height: "inherit",
        marginLeft: "10px",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "flex-start",


    },

    notifType: {

        height: "20px",
        color: "#192452",
        fontFamily: 'Gilroy',
        fontSize: "15px",
        fontWeight: "400",
        lineHeight: "20px",
        textAlign: "left"
    },

    date: {


        color: "#9d9bb6",
        fontFamily: 'Gilroy',
        fontSize: "13px",
        fontWeight: "400",
        lineHeight: "17px"

    },



});


export const TodayEvent = ({type,date}) => {

    const classes = useStyles();

    function renderEvent(param) {
        switch(param) {
          case "1":
            return <Typography classes={{root:classes.notifType}} >Webinar: the basics ofâ€¦</Typography>;
          case "2":
            return <Typography classes={{root:classes.notifType}} >Team Building ActivityðŸ”¥</Typography>;
        }
      }


    return(<ButtonBase className={classes.eventContainer}>

                <img src={"./img/thumbFinal.png"} className={classes.notImage} alt=""/>

                <div className={classes.info}>

                   <Typography classes={{root:classes.date}}>{date}</Typography>

                    {
                    renderEvent(type)
                    }



                </div>

          </ButtonBase>
    );
}

TodayEvent.propTypes = {

    type: PropTypes.string,
    date: PropTypes.string,

}
